import { Component, OnInit } from '@angular/core';
import { MyserveService } from '../myserve.service';

@Component({
  selector: 'app-breaking',
  templateUrl: './breaking.component.html',
  styleUrls: ['./breaking.component.css']
})
export class BreakingComponent implements OnInit {

currentDate = new Date();

Hdata;
HNewsData;

constructor(private singh:MyserveService) { }

 ngOnInit() {  
    this.singh.fetchLatestNews().subscribe(res=> {
      //console.log(res);
      this.Hdata=res;
      this.HNewsData=this.Hdata.items;
    })
  }
}
