import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { AppComponent } from './app.component';
import { DateComponent } from './date/date.component';
import { MyserveService } from './myserve.service';
import { LatestComponent } from './latest/latest.component';
import { BreakingComponent } from './breaking/breaking.component';
import { TwoLatestComponent } from './two-latest/two-latest.component';


const appRoutes: Routes = [
  { path: 'ln', component: LatestComponent },
  { path: 'bn', component: BreakingComponent },
  { path: '2ln', component: TwoLatestComponent },
  { path: 'ln', redirectTo: '/', pathMatch: 'full' },
  { path: '', component: LatestComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    DateComponent,
    LatestComponent,
    BreakingComponent,
    TwoLatestComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
	RouterModule.forRoot(appRoutes)
    //RouterModule.forRoot(appRoutes, {useHash: true})
    ],
  providers: [ MyserveService ],
  bootstrap: [AppComponent]
})
export class AppModule { }
